@charset "UTF-8";
/* # =================================================================
   # Forms
   # ================================================================= */
input {
  border-radius: 0;
}

/* Replace pointer cursor in disabled elements */
[disabled] {
  cursor: default;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto; /* Correct the cursor style of increment and decrement buttons in Chrome */
}

[type=search] {
  -webkit-appearance: textfield; /* Correct the odd appearance in Chrome and Safari */
  outline-offset: -2px; /* Correct the outline style in Safari */
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; /* Remove the inner padding in Chrome and Safari on macOS */
}

textarea {
  overflow: auto; /* Internet Explorer 11+ */
  resize: vertical; /* Specify textarea resizability */
}

button,
input,
optgroup,
select,
textarea {
  font: inherit; /* Specify font inheritance of form elements */
}

optgroup {
  font-weight: bold; /* Restore the font weight unset by the previous rule */
}

button {
  overflow: visible; /* Address `overflow` set to `hidden` in IE 8/9/10/11 */
}

button,
select {
  text-transform: none; /* Firefox 40+, Internet Explorer 11- */
}

/* Apply cursor pointer to button elements */
button,
[type=button],
[type=reset],
[type=submit],
[role=button] {
  cursor: pointer;
  color: inherit;
}

/* Remove inner padding and border in Firefox 4+ */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/* Replace focus style removed in the border reset above */
button:-moz-focusring,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  outline: 1px dotted ButtonText;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button; /* Correct the inability to style clickable types in iOS */
}

/* Remove the default button styling in all browsers */
button,
input,
select,
textarea {
  background-color: transparent;
  border-style: none;
}

/* Style select like a standard input */
select {
  -moz-appearance: none; /* Firefox 36+ */
  -webkit-appearance: none; /* Chrome 41+ */
}

select::-ms-expand {
  display: none; /* Internet Explorer 11+ */
}

select::-ms-value {
  color: currentColor; /* Internet Explorer 11+ */
}

legend {
  border: 0; /* Correct `color` not being inherited in IE 8/9/10/11 */
  color: inherit; /* Correct the color inheritance from `fieldset` elements in IE */
  display: table; /* Correct the text wrapping in Edge and IE */
  max-width: 100%; /* Correct the text wrapping in Edge and IE */
  white-space: normal; /* Correct the text wrapping in Edge and IE */
  max-width: 100%; /* Correct the text wrapping in Edge 18- and IE */
}

::-webkit-file-upload-button {
  /* Correct the inability to style clickable types in iOS and Safari */
  -webkit-appearance: button;
  color: inherit;
  font: inherit; /* Change font properties to `inherit` in Chrome and Safari */
}

body {
  font-family: "Meiryo", "Hiragino Kaku Gothic ProN", "Hiragino Sans", sans-serif;
  font-size: 12px;
  color: #2B1D0E;
}

a:hover {
  opacity: 0.6;
}

.l-section {
  margin-top: 60px;
  margin-bottom: 100px;
}

.l-inner {
  width: 1228px;
  max-width: 100%;
  height: inherit;
  margin: 0 auto;
  padding-right: 4%;
  padding-left: 4%;
}
@media screen and (max-width: 767px) {
  .l-inner {
    padding-left: 24px;
    padding-right: 24px;
  }
  #fv .l-inner {
    padding-left: 0;
    padding-right: 0;
  }
}

.c-info__list {
  display: grid;
  row-gap: 16px;
}
@media screen and (max-width: 767px) {
  .c-info__list {
    row-gap: 32px;
  }
}

.c-info__link {
  display: flex;
  column-gap: 32px;
  color: #2B1D0E;
}
@media screen and (max-width: 767px) {
  .c-info__link {
    flex-direction: column;
    row-gap: 8px;
  }
}

.c-info__date {
  letter-spacing: 0.1em;
}

.c-info__title {
  letter-spacing: 0.1em;
  width: 100%;
  overflow-wrap: anywhere;
}

.c-coupon:hover {
  opacity: 0.6;
}

.c-coupon__link {
  width: 172px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.c-coupon__link:hover {
  opacity: 1;
}

.c-coupon__frame {
  width: 168px;
  height: 38px;
  line-height: 38px;
  border: 1px solid #4cc764;
  color: #4cc764;
  border-radius: 10px;
}

.c-coupon__txt {
  color: #4cc764;
  font-size: 10px;
  font-weight: 700;
  padding: 0 8px;
}

.c-coupon__icon {
  display: block;
  position: absolute;
  background: no-repeat center center/cover;
  width: 50px;
  aspect-ratio: 1/1;
  right: 0;
}

.c-ask {
  width: 180px;
  background-color: #4cc764;
  border-radius: 10px;
}

.c-ask__link {
  padding: 8px;
  display: flex;
  align-items: center;
}

.c-ask__icon {
  display: block;
  background: no-repeat center center/cover;
  width: 40px;
  aspect-ratio: 1/1;
}

.c-ask__txt {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
}

.c-table {
  width: 100%;
  border-collapse: collapse;
  margin: 50px 0;
  font-size: 12px;
  text-align: left;
  border: 1px solid #ddd; /* Table border */
}

.c-table__body th {
  text-align: center;
}

.c-table__row {
  border: 1px solid #ddd;
}
.c-table__row th, .c-table__row td {
  border: 1px solid #ddd; /* Header cell border */
  padding: 2em 1.25em;
  letter-spacing: 0.1em;
}
.c-table__row th {
  background-color: transparent;
}

.c-mail__ttl {
  font-size: 10px;
  color: #B2A59A;
}

.c-mail__from {
  margin: 1.25em 0 0;
  display: grid;
  row-gap: 1.25em;
}
.c-mail__from input[type=mail] {
  width: 100%;
  border: 1px solid #2B1D0E;
  padding: 1em;
  color: #B2A59A;
}

.c-mail__btn {
  width: 118px;
  height: 24px;
  line-height: 24px;
  color: #fff;
  border-radius: 32px;
  background-color: #B2A59A;
}
.c-mail__btn:hover {
  opacity: 0.6;
}

.c-about__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 767px) {
  .c-about__head {
    display: block;
  }
}

.c-about__ttl {
  font-size: 10px;
  color: #B2A59A;
}

.c-about__sns {
  color: #B2A59A;
}
.c-about__sns svg {
  font-size: 20px;
  padding: 4px;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .c-about__sns svg {
    padding: 8px;
    font-size: 32px;
  }
}
.c-about__body .c-about__sns {
  margin-top: 2em;
}

.c-about__lead {
  overflow-wrap: break-word;
}

.c-nav__ttl {
  font-size: 10px;
  color: #B2A59A;
}

.c-nav__list {
  display: grid;
  row-gap: 1.25em;
}

/*===================================================
# news
===================================================*/
.p-news {
  margin-top: 40px;
  background-color: #B2A59A;
}

.p-news__list {
  width: 100%;
  max-width: 357px;
  margin: 40px auto 0;
}
@media screen and (max-width: 767px) {
  .p-news__list {
    padding: 0 24px;
  }
}

/*===================================================
# section
===================================================*/
.p-section__inner {
  padding: 40px 0;
}

.p-section__title {
  font-size: 20px;
  text-align: center;
}

/*===================================================
# fv
===================================================*/
.p-fv {
  background: url(../img/img-fv.webp) no-repeat center center;
  background-size: cover;
  height: 40vw;
  position: relative;
}
@media screen and (max-width: 767px) {
  .p-fv {
    height: 100vw;
  }
}

.p-fv__logo {
  position: absolute;
  width: 238px;
  height: 93px;
  position: absolute;
  content: "";
  top: 7.64vw;
  left: 50%;
  transform: translateX(-50%);
}
.p-fv__logo img {
  width: 100%;
}

/*===================================================
# footer
===================================================*/
.p-footer__upper {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 767px) {
  .p-footer__upper {
    flex-direction: column;
  }
}

.p-footer__downer {
  margin-top: 32px;
}
.p-footer__downer > :nth-child(2) {
  margin-top: 16px;
}

@media screen and (max-width: 767px) {
  .p-footer__block {
    max-width: 100%;
  }
  .p-footer__block:not(:first-child) {
    margin-top: 3em;
  }
}

.p-footer__block--first {
  max-width: 374px;
  width: 100%;
}
.p-footer__block--first > :nth-child(2) {
  margin-top: 100px;
}
@media screen and (max-width: 767px) {
  .p-footer__block--first > :nth-child(2) {
    margin-top: 3em;
  }
}

@media screen and (min-width: 768px) {
  .p-footer__block-body {
    margin-top: 32px;
  }
}
@media screen and (max-width: 767px) {
  .p-footer__block-body {
    overflow: hidden; /* 子要素が領域外に出た場合に隠す */
    max-height: 0; /* 閉じている時はmax-heightを0に */
    transition: max-height 0.3s linear, margin-top 0.3s linear; /* max-heightの変化にアニメーション効果を追加 */
  }
  .is-open .p-footer__block-body {
    margin-top: 32px;
    max-height: 550px;
  }
}

@media screen and (max-width: 767px) {
  .p-footer__block-ttl {
    padding: 1em 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #B2A59A;
    cursor: pointer;
  }
  .p-footer__block-ttl::after {
    position: absolute;
    content: "";
    display: block;
    width: 4px;
    aspect-ratio: 1/1;
    border-top: 2px solid #B2A59A;
    border-left: 2px solid #B2A59A;
    transform: rotate(225deg);
    right: 1em;
  }
  .is-open .p-footer__block-ttl::after {
    transform: rotate(45deg);
  }
}

@media screen and (max-width: 767px) {
  .p-footer__sns {
    margin-top: 24px;
  }
  .p-footer__block-body .p-footer__sns {
    text-align: center;
  }
}

.p-footer__payment {
  padding: 8px;
  max-width: 100%;
  width: 400px;
  background-color: skyblue;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .p-footer__payment {
    width: 100%;
  }
}

.p-footer__coupon {
  display: grid;
  justify-content: end;
}

.p-footer__copy {
  margin-top: 2em;
  text-align: center;
}

/*
 * 非表示
 */
@media screen and (min-width: 768px) {
  .u-hidden-pc {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .u-hidden-sp {
    display: none;
  }
}

.u-icon--line {
  background-image: url(../img/icon-line.webp);
}