@use "../global" as global;

.c-coupon {

	&:hover {
		opacity: .6;
	}
}

.c-coupon__link {
  width: calc(148px + 24px);
  position: relative;
  display: flex;
  align-items: center;
	justify-content: space-between;

	&:hover {
		opacity: 1;
	}
}

.c-coupon__frame {
  width: 168px;
  height: 38px;
  line-height: 38px;
	border: 1px solid map-get(global.$color, "line");
  color: map-get(global.$color, "line");
  border-radius: 10px;
}

.c-coupon__txt {
  color: map-get(global.$color, "line");
  font-size: 10px;
  font-weight: 700;
  padding: 0 8px;
}

.c-coupon__icon {
  display: block;
  position: absolute;
  background: no-repeat center center/cover;
  width: 50px;
  aspect-ratio: 1/1;
  // right: -24px;
	right: 0;
}
