@use "../global" as global;

.c-table {
	width: 100%;
	border-collapse: collapse;
	margin: 50px 0;
	font-size: global.$fz-base;
	text-align: left;
	border: 1px solid #ddd;  /* Table border */
}

.c-table__head {

}

.c-table__body {
	th {
		text-align: center;
	}
}

.c-table__row {
	border: 1px solid #ddd;

	th, td {
		border: 1px solid #ddd;  /* Header cell border */
		padding: 2em 1.25em;
		letter-spacing: .1em;
	}

	th {
		background-color: transparent;
	}
}
