@use "../global" as global;

.c-info__list{
	display: grid;
	row-gap: 16px;

	@include global.mq(sp) {
		row-gap: 32px;
	}
}

.c-info__item{
}

.c-info__link{
	display: flex;
	column-gap: 32px;
	color: map-get(global.$color, "main-txt");

	@include global.mq(sp) {
		flex-direction: column;
		row-gap: 8px;
	}
}

.c-info__date{
	letter-spacing: 0.1em;
}

.c-info__title{
	letter-spacing: 0.1em;
	width: 100%;
	overflow-wrap: anywhere;
}
