@use "../global" as global;

.c-ask {
	width: 180px;
	background-color: map-get(global.$color, 'line');
	border-radius: 10px;
}

.c-ask__link {
	padding: 8px;
	display: flex;
	align-items: center;
}

.c-ask__icon {
	display: block;
	background: no-repeat center center/cover;
	width: 40px; //40pxは公式の推奨最低サイズ
	aspect-ratio: 1/1;
}

.c-ask__txt {
	font-size: 20px;
	font-weight: 700;
	color: map-get(global.$color, 'white');
}