@use "../global" as global;
/*===================================================
# footer
===================================================*/
.p-footer {
}

.p-footer__inner {
}

.p-footer__upper {
  display: flex;
  justify-content: space-between;

  @include global.mq(sp) {
    flex-direction: column;
  }
}

.p-footer__downer {
  margin-top: 32px;

  > :nth-child(2) {
    margin-top: 16px;
  }
}

.p-footer__block {

	@include global.mq(sp) {
		&:not(:first-child) {
			margin-top: 3em;
		}

		max-width: 100%;
	}
}

.p-footer__block--first {
  max-width: 374px;
  width: 100%;

  > :nth-child(2) {
    margin-top: 100px;

		@include global.mq(sp) {
			margin-top: 3em;
		}
  }
}

.p-footer__block-head {
}

.p-footer__block-body {
	@include global.mq(pc) {
		margin-top: 32px;
	}

	@include global.mq(sp) {
		overflow: hidden; /* 子要素が領域外に出た場合に隠す */
    max-height: 0; /* 閉じている時はmax-heightを0に */
    transition: max-height .3s linear, margin-top .3s linear; /* max-heightの変化にアニメーション効果を追加 */

		.is-open & {
			margin-top: 32px;
			max-height: 550px;
		}
	}
}

.p-footer__block-ttl {

  @include global.mq(sp) {
    padding: 1em 0;
    position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-top: 1px solid map-get(global.$color, "point");
		cursor: pointer;

		// アコーディオンのアイコン
    &::after {
      position: absolute;
      content: "";
      display: block;
      width: 4px;
      aspect-ratio: 1/1;
      border-top: 2px solid map-get(global.$color, "point");
      border-left: 2px solid map-get(global.$color, "point");
      transform: rotate(225deg);
			right: 1em;

			.is-open & {
				transform: rotate(45deg);
			}
    }
  }
}

.p-footer__sns {
	@include global.mq(sp) {
		margin-top: 24px;

		.p-footer__block-body & {
			text-align: center;
		}
	}
}

.p-footer__payment {
  padding: 8px;
  max-width: 100%;
  width: 400px; // 仮
  background-color: skyblue; //仮
  margin-left: auto;

  @include global.mq(sp) {
    width: 100%;
  }
}

.p-footer__coupon {
  display: grid;
  justify-content: end;
}

.p-footer__copy {
  margin-top: 2em;
  text-align: center;
}
