@use "../global" as global;

.c-about {

}

.c-about__head {
	display: flex;
	align-items: center;
	justify-content: space-between;

	@include global.mq(sp) {
		display: block;
	}
}

.c-about__ttl {
	font-size: 10px;
	color: map-get(global.$color, "point");
}

.c-about__sns {
	color: map-get(global.$color, "point");
	
	svg {
		font-size: 20px;
		padding: 4px;
		cursor: pointer;

		@include global.mq(sp) {
			padding: 8px;
			font-size: 32px;
		}
	}

	.c-about__body & {
		margin-top: 2em;
	}
}

.c-about__lead {
	overflow-wrap: break-word;
}

