@use "../global" as global;
/*===================================================
# fv
===================================================*/

.p-fv{
	background: url(../img/img-fv.webp) no-repeat center center;
	background-size: cover;
	height: 40vw;
	position: relative;

	@include global.mq(sp) {
		height: 100vw;
	}
}

.p-fv__logo{
	position: absolute;
	width: 238px;
	height: 93px;
	position: absolute;
	content: "";
	top: 7.64vw; //110px
	left: 50%;
	transform: translateX(-50%);

	img {
		width: 100%;
	}
}