@use "../global" as global;

.c-mail {

}

.c-mail__ttl {
	font-size: 10px;
	color: map-get(global.$color, "point");
}

.c-mail__lead {
}

.c-mail__from {
	margin: 1.25em 0 0;
	display: grid;
	row-gap: 1.25em;

	input[type=mail] {
		width: 100%;
		// height: 38px;
		border: 1px solid map-get(global.$color, "main-txt");
		padding: 1em;
		color: map-get(global.$color, "point");
	}
}

.c-mail__btn {
	width: 118px;
	height: 24px;
	line-height: 24px;
	color: map-get(global.$color, "white");
	border-radius: 32px;
	background-color: map-get(global.$color, "point");
	
	&:hover {
		opacity: .6;
	}
}